import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem,
		uniLoadMore,
		empty
	},
	data() {
		return {
			source: 0,
			awardList: [],
			pageNo: 1,
			pageSize: 20,
			loadingType:''
		}
	},
	onLoad(option) {
		this.searchUserAward();
	},
	onReachBottom() {
		this.loadMore();
	},
	//下拉刷新
	onPullDownRefresh() {
		//重新加载数据
		this.resetPage();
		this.searchUserAward();
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//加载更多
		loadMore() {
			if (this.loadingType === 'more') {
				this.pageNo = this.pageNo + 1;
				this.searchUserAward();
			}
		},
		resetPage() {
			this.pageNo = 1;
			this.awardList = [];
		},
		//添加或修改成功之后回调
		refreshList() {
			this.inquiryaward();
		},
		//查询用户奖金明细
		searchUserAward() {
			let that = this;
			let keyArray = ['USER'];
			let searchOptions = {
				userUuid: this.userInfo.userUuid,
				startIndex: (this.pageNo - 1) * this.pageSize,
				pageSize: this.pageSize
			};
			searchOptions.keyArray = keyArray;
			this.loadingType = 'loading';
			this.$api.request.getBalanceList(searchOptions, res => {
				if (res.body.status.statusCode === '0') {
					var total = res.body.data.total;
					var awardList = res.body.data.awards;
					this.awardList = this.awardList.concat(awardList);
					if (this.awardList.length >= total) {
						this.loadingType = 'noMore';
					} else {
						this.loadingType = 'more';
					}
				} else {
					console.log(res.body.status.errorDesc);
				}
			}, true);
		}
	}
}
